  transition: all 5000s ease-in-out;
}

.top {
  min-height: calc(90 * var(--vh));
  background-image: url(../images/main-bg.png);
  background-size: cover;
  background-position: top right;
  background-repeat: no-repeat;
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.top__wrapper {
  width: 100%;
  display: flex;
}
.top__inner {
  width: 50%;
  margin: auto auto auto 0;
}
.top__title-top {
  margin: 0;
  font-weight: 700;
  font-size: 50px;
  line-height: 1.1;
}
.top__title-bottom {
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 0;
  margin-top: 8px;
}
.top__descr {
  font-size: 18px;
  line-height: 24px;
  color: rgba(239, 242, 247, 0.5);
}
.top__quiz-link {
  display: block;
  margin: 30px 0;
  font-size: 20px;
  line-height: 1;
  padding: 22px 40px;
  border-radius: 13px;
  background-color: #f8d44d;
  width: fit-content;
  color: #000000;
  text-align: center;
  font-weight: 500;
}

.footer {
  width: 100%;
  min-height: calc(10 * var(--vh) + 40px);
  background-color: #1B202B;
  border-radius: 40px 40px 0 0;
  margin-top: -40px;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 10px;
}
.footer__link {
  position: relative;
  font-size: 14px;
  line-height: 1.4;
  display: inline-block;
  text-align: center;
}
.footer__link::before {
  content: url(../images/footer-link.png);
  display: block;
}

@media (max-width: 1200px) {
  .top {
    background-position: bottom right -200px;
  }
  .top__inner {
    width: unset;
    max-width: 600px;
  }
}
@media (max-width: 900px) {
  .top {
    min-height: calc(110 * var(--vh));
    background-image: url(../images/main-bg-tablet.jpg);
    justify-content: start;
    background-position: bottom right;
  }
  .top__inner {
    margin: 0 auto;
    text-align: center;
  }
  .top__title-top {
    font-size: 30px;
  }
  .top__title-bottom {
    font-size: 16px;
  }
  .top__quiz-link {
    width: 100%;
    border-radius: 32px;
  }

  .footer__link {
    font-size: 12px;
  }
}
@media (max-width: 600px) {
  .top {
    background-position: bottom right;
  }
  .top__title-bottom {
    margin-top: 15px;
    font-size: 14px;
  }
  .top__quiz-link {
    font-size: 16px;
    font-weight: 600;
  }
  .top__descr {
    color: rgba(239, 242, 247, 0.8);
  }

  .footer {
    padding-top: 30px;
  }
}
.webp .top { background-image: url(../images/main-bg.webp); }
@media (max-width: 900px) {
  .webp .top { background-image: url(../images/main-bg-tablet.webp); }
}